import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import logo from "../../assets/img/logo.jpg";
import styles from "./header.module.scss";
//library classnames!!!!!!
const classes0 = styles.menuItem + " " + styles.search;
const classes1 = styles.menuItem + " " + styles.cart;
const classes2 = styles.menuItem + " " + styles.signIn;

const Header = () => {
  return (
    <header className={styles.header}>

      <div>
        <NavLink to="/main-page">
          <img src={logo} />
        </NavLink>
      </div>

      <nav className={styles.navbar} role="navigation">
        <ul className={styles.navList}>
          <li className={styles.navItem}>
            <NavLink to="/catalog">Footwear</NavLink>
          </li>
          <li className={styles.navItem}>
            <a href="/">Clothing</a>
          </li>
          <li className={styles.navItem}>
            <a href="/">Accessories</a>
          </li>
          <li className={styles.navItem}>
            <a href="/">Brands</a>
          </li>
          <li className={styles.navItem}>
            <a href="/">Clearance</a>
          </li>
        </ul>
        <select className={styles.navSelect}>
          <option>Footwear</option>
          <option>Clothing</option>
          <option>Accessories</option>
          <option>Brands</option>
          <option>Clearance</option>
        </select>
      </nav>

      <div className={styles.menu}>
        <div className={classes0}>
          <span>Search</span>
        </div>
        <div className={classes1}>
          <span>Cart</span>
        </div>
        <div className={classes2}>
          <span>Sign In</span>
        </div>
      </div>

    </header>
  );
};
export default Header;
