import React from "react";
import "./App.scss";
import Header from "./component/Header/Header";
import MainPage from "./component/MainPage/MainPage";
import CatalogFootWear from "./component/Catalog/CatalogFootWear";
import Product from "./component/Catalog/Product/Product";
import Informations from "./component/Informations/Informations";
import Footer from "./component/Footer/Footer";
import Filter from "./component/Filter/Filter";
import { Redirect, Route, Switch } from "react-router-dom";

const App = () => {
  return (
    <div className="App">
      <Header />
      <Switch>
        <Route path="/catalog" render={() => <CatalogFootWear />} />
        <Route path="/product/:id?" render={() => <Product />} />
        <Route path="/main-page" render={() => <MainPage />} />
        <Informations />
        {/* <Filter /> */}
        <Redirect exact from="/" to="/main-page" />
        <Route path="*" render={() => <div>404</div>} />
      </Switch>
      <Footer />
    </div>
  );
};

export default App;
